export const generateDataLayer = ({ appData, activePage }) => {
	if (typeof activePage !== 'string') {
		activePage = activePage?.id;
	}
	let country = appData.meta?.region?.slug;
	if (country === 'gb') {
		country = 'uk';
	}
	const siteName = appData.meta?.title?.slug;

	if (activePage.startsWith('bolt')) {
		activePage = activePage.substring(4);
	}

	if (activePage === 'showtimes') {
		activePage = 'tickets';
	}

	const isAuRegion = __REGION__ === 'au' || __REGION__ === 'nz';
	const propertyCode = appData.meta?.title?.alternateSlug || '';

	let dataLayerString = `const _dl = {
		site: {
			'site': 'powster',
			'region': '${isAuRegion ? 'aus' : 'emea'}',
			'country': '${country}',
			'language': '${appData.meta?.region?.language || 'en'}',
			'portal': '${isAuRegion ? 'aus international' : 'emea-disney'}',
			'cms_version': 'powster',
			'otguid': '${appData.options?.ensightenOneTrustGUID || ''}',
			'cds': '',
		},
		'page': {
			'page_url': window.location.href,
			'page': window.location.pathname.replace(${/\//g}, ':').replace(${/^\:|\:$/}, '') || '',
			'page_title': document.title,
			'platform': 'web',
			'slug': window.location.pathname,
			'movieName': '${siteName}',
			'propertyCode': '${propertyCode}'
		},
		'pzn': {},
		'visitor': {},
		'privacy': {
			${isAuRegion ? `` : `'cmp': 'gdpr',`}
		},
		'pageViewed': false
	};

	let countryCode;
	if ('${country}' === 'uk') {
		countryCode = ':gb';
	} else {
		countryCode = ":" + '${country}';
	}
	const pageName = _dl.page.page.replace(countryCode, '');
	_dl.page.page_name = _dl.site.region + ":" + _dl.site.country + ":" + _dl.site.site + ":" + pageName;

	_dl.subscribe = (eventName, callback = () => {}) => {
		document.addEventListener('__dataLayer.' + eventName, (e) => {
			if (e && e.detail) callback(e.detail);
		});
	};

	_dl.publish = (eventName, data) => {
		const event = new CustomEvent('__dataLayer.' + eventName, {
			detail: data || []
		});
		document.dispatchEvent(event);
	};

	_dl.trackPage = () => {
		window.__dataLayer.pageViewed = true;
		window.__dataLayer.publish('pageView');
		document.dispatchEvent(new Event('dataLayerReady'));
	};

	_dl.OneTrustDialog = (t) => {
		const n = window.OneTrust;
		if (n && n.loaded) {
			t.preventDefault();
			n.ToggleInfoDisplay();
		}
	};
	window.__dataLayer = window.__dataLayer || {};
	Object.assign(window.__dataLayer, _dl);
	`;

	return dataLayerString;
};