import { h } from 'preact';
import Store from 'store/store';
import { joinClasses } from 'utils/utils';
import * as BaseComp from 'components/bolt/header/header.js';
import Button from 'components/bolt/button/button';
import s from 'components/bolt/header/header.sss';

export default class Header extends BaseComp.default {

	renderHeaderLogo() {
		const headerLogo = this.props?.appData?.header?.powsterHeader?.logo;
		const headerLogoExclusions = (headerLogo?.pageExclusions || '').split(',')?.map(e => e.trim())?.filter(e => e);
		const hideHeaderLogo = headerLogoExclusions.includes(Store.get().activePage);
		const className = joinClasses(s.headerLogo, hideHeaderLogo && s.hide, !headerLogo.path && s.unclickable);
		const content = <img src={headerLogo.src} alt={headerLogo.alt} />;

		if (/^(https?:\/\/|mailto:)/i.test(headerLogo.path)) {
			return (
				<a
					class={className}
					href={headerLogo.path}
					target={headerLogo.sameWindow ? '_self' : '_blank'}
					rel="noopener noreferrer"
					onClick={() => this.trackPersistentButtonClick(headerLogo.path, headerLogo.alt || 'headerLogo')}
					key="headerLogoLink"
				>
					{content}
				</a>
			);
		}

		if (headerLogo) {
			return (
				<Button
					class={className}
					trigger={e => headerLogo.path && this.triggerPersistentButton(headerLogo.path, 'Logo', headerLogo.sameWindow, e)}
					key="headerLogo"
				>
					{content}
				</Button>
			);
		}
	}
}