import { h, Component } from 'preact';
import { accessNested, asList } from 'utils/utils';
import formatDateTime from 'utils/formatDateTime';
import pure from 'utils/pure';

function persons(obj) {
	if (!obj) return null;
	if (obj.name) {
		obj = [obj];
	} else if (typeof obj === 'string') {
		if (obj.includes(',')) {
			obj = Object.assign({}, obj.split(',').map(e => e.trim().replace(/^and /g, '')));
		} else {
			obj = [obj];
		}
	}
	return Object.values(obj).filter(e => e).map(person => {
		let obj = {
			'@type': 'Person'
		};
		if (typeof person === 'string') {
			obj.name = person;
		} else {
			Object.assign(obj, person);
		}
		return obj;
	});
}

export default @pure class Seo extends Component {

	getExtraContent() { }

	render(props) {
		const {
			appData,
			shareImage
		} = props;

		// http://schema.org/Movie

		if (!appData) return;
		let naiveReleaseDate  = new Date(appData.meta.dates.release);
		let localeReleaseDate = new Date(naiveReleaseDate.getTime() + naiveReleaseDate.getTimezoneOffset() * 60 * 1000);
		const releaseDate 	  = formatDateTime(localeReleaseDate, 'yyyy-MM-dd');

		// appData.doc.googleSchema is read-only so making copy;
		let schema 			   = JSON.parse(JSON.stringify(appData.doc.googleSchema || {}));
		const { baseCopy }     = this.props.appData.copy;

		let actorsContent      = baseCopy[schema.actorList] || schema.actorList;
		schema.actors          = schema.actors ? schema.actors : (schema.actor ? schema.actor : persons(actorsContent));
		delete schema.actorList;
		delete schema.actor;

		let directorContent    = baseCopy[schema.directorList] || schema.directorList;
		schema.director        = schema.director ? schema.director : persons(directorContent);
		delete schema.directorList;

		schema.description = (baseCopy[schema.description] || schema.description || '').replace(/(\r?\n|\r)/g, '').replace(/\s+/g, ' ');

		schema.dateCreated = schema.dateCreated ? schema.dateCreated.replace(/%START_DATE%/g, releaseDate) : releaseDate;

		schema.releasedEvent = schema.releasedEvent || {};
		schema.releasedEvent.startDate = schema.releasedEvent.startDate ? schema.releasedEvent.startDate.replace(/%START_DATE%/g, releaseDate) : releaseDate;
		if (schema.releasedEvent.location && schema.releasedEvent.location.name) {
			schema.releasedEvent.location.name = schema.releasedEvent.location.name.toUpperCase();
		}
		schema.url = schema.url || appData.meta.url;
		schema.name = schema.name || appData.meta.title.locale || appData.meta.title.en;
		schema.image = schema.image ? schema.image.replace(/%META_SHARE_IMAGE%/g, shareImage) : (shareImage || "%IMAGE_REGION_ROOT%/share.png");

		if (schema.sameAs && typeof schema.sameAs === 'string') {
			const facebook  = accessNested(appData, 'header.powsterHeader.follow.facebook.destination', '');
			const twitter   = accessNested(appData, 'header.powsterHeader.follow.twitter.destination', '');
			const instagram = accessNested(appData, 'header.powsterHeader.follow.instagram.destination', '');

			const socials = schema.sameAs.split(', ').map(social => {
				return social.replace(/%FACEBOOK%/g, facebook).replace(/%TWITTER%/g, twitter).replace(/%INSTAGRAM%/g, instagram);
			}).filter(e => e);

			if (socials.length > 0) {
				schema.sameAs = socials;
			} else {
				delete schema.sameAs;
			}
		} else if (!schema.sameAs) {
			const social = appData.meta.social || {};
			schema.sameAs = Object.keys(social).map(key => social[key] && social[key].link).filter(e => e);
			if (!schema.sameAs.length) {
				delete schema.sameAs;
			}
		}

		// update schema for the trailer
		const list = asList(accessNested(appData, 'pages.boltVideos.data.list', []));
		const listHasVideo = list.find(item => item && item.youtubeId);

		if (listHasVideo) {
			schema.trailer = schema.trailer || {};
			schema.trailer.name 		= schema.trailer.name || listHasVideo.name;
			schema.trailer.thumbnailUrl = schema.trailer.thumbnailUrl || listHasVideo.thumbSrc;
			schema.trailer.url 			= schema.trailer.url || 'https://www.youtube.com/watch?v=' + listHasVideo.youtubeId;
			schema.trailer.description  = schema.trailer.description || listHasVideo.name;
			schema.trailer.uploadDate   = schema.trailer.uploadDate || releaseDate;
		}
		if (schema.trailer) {
			if (!schema.trailer.description)  delete schema.trailer['description'];
			if (!schema.trailer.name)         delete schema.trailer['name'];
			if (!schema.trailer.thumbnailUrl) delete schema.trailer['thumbnailUrl'];
			if (!schema.trailer.uploadDate)   delete schema.trailer['uploadDate'];
			if (!schema.trailer.url) {
				delete schema.trailer['url'];
				delete schema.trailer;
			}
		}

		return (
			<div>
				<script type="application/ld+json" dangerouslySetInnerHTML={{ __html: JSON.stringify(schema) }} />
				{this.getExtraContent()}
			</div>
		);
	}

}
