import { h } from 'preact';
import Store from 'store/store';
import { accessNested, asList } from 'utils/utils';
import { getPageMeta, getPageTitle } from 'utils/bolt/index';
import { generateDataLayer } from 'utils/gdpr/ensighten';
import { staticPixels } from 'pages/bolt_features';
import * as tracking from 'store/bolt/trackingV2';
import metaGeneratorTwitter from 'base/bolt/socialGenerators/twitter';
import metaGeneratorFB from 'base/bolt/socialGenerators/facebook';
import BoltApp from 'base/bolt/boltApp';
import Seo from 'components/bolt/seo/seo';
import Scripts from 'components/bolt/scripts/scripts';
import * as pages from 'pages/bolt_static_pages';
// import HTML from 'components/core/html/html';

function getExtraMeta(metaTags) {
	if (!metaTags || !metaTags.extraTags) {
		return [];
	}

	return Object.keys(metaTags.extraTags).map(property => {
		let content = metaTags.extraTags[property] && metaTags.extraTags[property].content;
		if (!content) return;
		let props = typeof content === 'object' ? content : { property, content };
		return <meta {...props} />;
	}).filter(e => e);
}

function renderScript(script) {
	if (typeof script === 'string') {
		return <script src={script} type="text/javascript" />;
	}
	if (typeof script !== 'object') {
		return;
	}
	const { content, ...props } = script;
	return <script dangerouslySetInnerHTML={{ __html: content }} {...props} />;
}

const Doc = ({ initialState, currentPage, extraScript, headHTML, relativeRoot, reload, type, appProps }) => {
	Store.set(initialState);
	const buildDate = Date.now();

	if (currentPage.relativeRootOffset) {
		relativeRoot = currentPage.relativeRootOffset + relativeRoot;
	}

	const { appData } = initialState;
	const { doc, copy } = appData;
	const pageId = currentPage.id;
	let metaData = getPageMeta({ store: initialState, page: pageId });

	let mainPageId = currentPage.id;
	if (currentPage.section) {
		// TODO: ability to attach sections to different pages
		mainPageId = Object.values(pages).find(page => page.page === 'home')?.id || 'boltHome';
	}
	const renderedPage = { id: mainPageId, getter: pages[mainPageId] };

	// Meta Data
	let { description, author, keywords, favicon, shareImage } = metaData;
	let title = getPageTitle({ store: initialState, page: pageId, meta: metaData });
	description = copy.baseCopy[description] || description;
	author = copy.baseCopy[author] || author;
	keywords = keywords && keywords.startsWith('$') ? copy.baseCopy[keywords] : keywords;
	// const imageRegion = accessNested(initialState, 'appData.urls.roots.imageRegion');

	let twitterMetaData = Object.assign({
		cardTitle: title,
		cardDescription: description,
		description
	}, metaData.twitterMeta);

	let facebookMetaData = Object.assign({
		title,
		description
	}, metaData.facebookMeta);

	if (shareImage && shareImage.startsWith('//')) {
		shareImage = 'https:' + shareImage;
	}

	let robotsIndex = appData.doc.metaTags.noIndex ? 'noindex' : 'index';
	let robotsFollow = appData.doc.metaTags.noFollow ? 'nofollow' : 'follow';
	const robots = robotsIndex + ', ' + robotsFollow;

	const initialStateSanitized = JSON.stringify(initialState)
		// Remove weird spaces breaking JSON.stringify used as JS - https://v8.dev/features/subsume-json
		.replace(/[\u2028\u2029]/g, '')
		// Ensure ' does not terminate the JSON.parse string
		.replace(/(['\\])/g, '\\$1')
		// Ensure </script> does not end the encompassing tag
		.replace(/<\/script>/ig, '<\\/script>')
		// Ensure <!-- does not start a comment
		.replace(/<!--/g, '<\\!--');

	// Setup for the app on load
	const defaultRelativeRoot = 'window.__RELATIVE_ROOT__ = ' + JSON.stringify(relativeRoot) + ';';
	let windowRelativeRoot = defaultRelativeRoot;
	if (currentPage.customRelativeRoot) {
		windowRelativeRoot = `try { window.__RELATIVE_ROOT__ = eval(${JSON.stringify(currentPage.customRelativeRoot)}); } catch (e) { ${defaultRelativeRoot} }`;
	}
	// let relativeRoot = currentPage.customRelativeRoot || JSON.stringify(relativeRoot);
	let initialDataValues = [
		windowRelativeRoot,
		'window.__INITIAL_STATE__ = JSON.parse(\'' + initialStateSanitized + '\');',
		'window.__BUILD_DATE__ = ' + buildDate + ';'
	];
	let initialStateData = [<script dangerouslySetInnerHTML={{ __html: initialDataValues.join('\n') }} />];

	if (extraScript) {
		initialStateData.push(extraScript);
	}

	favicon = favicon || {};
	let faviconIco = `${favicon.ico || 'favicon.ico'}?v=${buildDate}`;
	let faviconPng = `${favicon.png || 'favicon.png'}?v=${buildDate}`;

	const googleOptimize = accessNested(appData, 'options.googleOptimize');

	let initialDataLayer = {
		filmTitle: appData.meta.title.locale || appData.meta.title.en,
		domainCountry: appData.meta.region.slug
	};
	const sonyDataLayer = appData?.options?.sonyFeatures?.sonyDataLayer;
	if (sonyDataLayer?.contentType) {
		const titleSlug = accessNested(appData, 'meta.title.slug', '').replace(/-/g, '');
		let currentPageSlug = currentPage.id.replace('bolt', '');
		initialDataLayer.page_title = `${sonyDataLayer.pageTitle || titleSlug} ${currentPageSlug}`;
		initialDataLayer.division = sonyDataLayer.division || 'MP';
		initialDataLayer.property_title = sonyDataLayer.propertyTitle || titleSlug;
		initialDataLayer.content_type = sonyDataLayer.contentType;
		initialDataLayer.site_country = sonyDataLayer.siteCountry || 'US';
		initialDataLayer.genres = sonyDataLayer.genres;
	}
	if (sonyDataLayer?.gpmsId) {
		initialDataLayer.gpms_id = sonyDataLayer.gpmsId;
	}

	let dataLayerString = 'dataLayer = [' + JSON.stringify(initialDataLayer) + ', { visitAt: +new Date() }];';

	const newGA = accessNested(appData, 'apis.tracking.powsterGATracking');
	if (USE_LEGACY_TRACKING && newGA && asList(newGA).some(id => id.startsWith('UA-') || id.startsWith('G-'))) {
		dataLayerString += `
			window.dataLayer = window.dataLayer || [];
			function powsterGtag() { dataLayer.push(arguments); }
			powsterGtag('js', new Date());
		`;
	}

	const ensighten = accessNested(appData, 'options.enableEnsighten', false);
	let ensightenDataLayer = '';
	if (ensighten) {
		ensightenDataLayer = generateDataLayer(initialState);
	}

	const facebookSiteVerif = accessNested(appData, 'doc.verificationCodes.fbVerificationCode');
	const googleSiteVerif = asList(accessNested(appData, 'doc.verificationCodes.googleVerificationCode'));
	const evidon = accessNested(appData, 'options.requireUserConsent', false);
	const didomi = accessNested(appData, 'options.didomiCookieConsent.scripts', false);
	const hasExtraRegion = accessNested(appData, 'header.powsterHeader.regionSelector.extraRegions', {});
	const isCanadaEnglish = accessNested(appData, 'meta.region.countryName', '') === 'Canada' && accessNested(appData, 'meta.region.lang', '') === 'English';
	const extraHreflang = accessNested(appData, 'doc.extraHreflang.hreflang');
	const extraHreflangLink = accessNested(appData, 'doc.extraHreflang.link');

	const useSiteHostedShareImage = accessNested(appData, 'options.sonyFeatures.useSiteHostedShareImage', false) && type !== 'local';
	let canonicalBaseUrl = accessNested(appData, 'meta.url', '');
	if (__IS_DEMO__ || __IS_LOCAL__) {
		canonicalBaseUrl = [
			__IS_DEMO__ ? 'https://demo.pow.io/' : 'http://localhost:4010/preview/',
			__STUDIO__,
			__TITLE__,
			__REGION__,
		].join('/');
	}
	let canonical = canonicalBaseUrl + currentPage.path;
	if (currentPage.canonical) {
		canonical = currentPage.canonical;
	}
	facebookMetaData.url = canonical;

	if (useSiteHostedShareImage) {
		shareImage = canonicalBaseUrl + '/' + shareImage.split('/').pop();
		faviconIco = canonicalBaseUrl + '/' + faviconIco.split('/').pop();
		faviconPng = canonicalBaseUrl + '/' + faviconPng.split('/').pop();
	}

	let allStaticPixels = [
		staticPixels,
		tracking.getStaticPixels(appData?.tracking)
	].filter(e => e).join('\n');

	return (
		<html lang={appData.meta.region.language}>
			<head>
				{doc.topmostScripts && Object.values(doc.topmostScripts).map(renderScript)}
				<title>{title}</title>
				<meta charset="utf-8" />
				<meta name="description" content={description} />
				<meta name="author" content={author} />
				{keywords && <meta name="keywords" content={keywords} />}
				<script dangerouslySetInnerHTML={{ __html: `var ua=navigator.userAgent||'';var isIE=ua.indexOf('MSIE ')!==-1||ua.indexOf('Trident/')!==-1;if(isIE)location.href='${relativeRoot}browser-not-supported.html';` }} />
				<script dangerouslySetInnerHTML={{ __html: `window.startLoadTime = window.performance ? window.performance.now() : 0` }} />
				<link rel="stylesheet" href={relativeRoot + 'css/bolt.css?v=' + buildDate} />
				{doc.styleLinks && Object.keys(doc.styleLinks).flatMap(link => {
					if (typeof doc.styleLinks[link] !== 'string') return;
					return doc.styleLinks[link].split('\n').map(url => {
						let href = url.trim().replace(/&amp;/gi, '&');
						if (!href) {
							return;
						}
						if (href.startsWith('async:')) {
							href = href.slice(6);
							return <link rel="preload" href={href} as="style" onload="this.onload=null;this.rel='stylesheet'" />;
						}
						return <link rel="stylesheet" href={href} />;
					});
				})}
				{!!ensightenDataLayer && <script dangerouslySetInnerHTML={{ __html: ensightenDataLayer }} />}
				{ensighten && <script dangerouslySetInnerHTML={{ __html: `document.cookie = 'region=gdpr; path=/; SameSite=Lax';` }} />}
				{ensighten && <script src={`https://dcf.espn.com/TWDC-DTCI/${(__IS_LOCAL__ || __IS_DEMO__) ? 'stage' : 'prod'}/Bootstrap.js`} />}
				{!!dataLayerString && <script dangerouslySetInnerHTML={{ __html: dataLayerString }} />}
				{doc.headerScripts && Object.values(doc.headerScripts).map(renderScript)}
				{headHTML && <meta name="x-custom" content="" dangerouslySetInnerHTML={{ __html: headHTML }} />}
				{/* defer: starts fetching as soon as seen and execute once the HTML parsing is done */}
				<script defer src={relativeRoot + 'js/bolt.js?v=' + buildDate} charset="utf-8" />

				{evidon?.companyId && <script companyid={evidon.companyId} src="https://c.evidon.com/dg/dg.js" />}
				{didomi && <script dangerouslySetInnerHTML={{ __html: `${didomi}` }} />}
				{googleOptimize && <script async src={`https://www.googleoptimize.com/optimize.js?id=${googleOptimize}`} />}

				<link rel="shortcut icon" type="image/png" href={faviconPng} />
				<link rel="icon" type="image/x-icon" href={faviconIco} />
				<link rel="canonical" href={canonical} />
				<meta name="viewport" content={(doc.viewport && typeof doc.viewport === 'string') ? doc.viewport : 'width=device-width, initial-scale=1'} />
				<meta http-equiv="X-UA-Compatible" content="IE=edge,chrome=1" />
				<meta name="apple-touch-fullscreen" content="yes" />
				<meta name="apple-mobile-web-app-capable" content="yes" />
				<meta name="mobile-web-app-capable" content="yes" />

				<link rel="apple-touch-icon" href={shareImage} />

				{hasExtraRegion && isCanadaEnglish && (
					<link rel="alternate" href={canonicalBaseUrl} hreflang={appData.meta.region.language + '-' + appData.meta.region.slug} />
				)}
				{extraHreflang && <link rel="alternate" href={extraHreflangLink} hreflang={extraHreflang} />}

				{!!doc.ogLocale && <meta property="og:locale" content={doc.ogLocale} />}
				{getExtraMeta(metaData)}
				{facebookSiteVerif && <meta name="facebook-domain-verification" content={facebookSiteVerif} />}
				{metaGeneratorFB(facebookMetaData, copy.baseCopy, type, useSiteHostedShareImage, canonicalBaseUrl)}
				{metaGeneratorTwitter(twitterMetaData, copy.baseCopy, type, useSiteHostedShareImage, canonicalBaseUrl)}
				<meta name="robots" content={robots} />
				<meta name="GOOGLEBOT" content={robots} />
				{googleSiteVerif.map(key => <meta name="google-site-verification" content={key} />)}
				<style type="text/css" id="frameBusterCSS" dangerouslySetInnerHTML={{ __html: `.frameBuster { display: none !important; }` }} />
			</head>

			<body dir={accessNested(appData, 'styling.fontVariables.textDirection') || 'ltr'}>
				<div id="app">
					<BoltApp {...appProps} renderedPage={renderedPage} />
				</div>

				{reload}
				{initialStateData}

				<script
					dangerouslySetInnerHTML={{
						__html: `
document.write=s=>document.body&&(document.body.appendChild(document.createElement('div')).innerHTML=s);
let b;self===top&&(b=document.getElementById('frameBusterCSS'))&&b.remove()`
					}}
				/>

				<Scripts appData={appData} page={currentPage} />

				{/* BR trick to include the staticPixels directly in the body (react can't put them inside cause it's a void element) */}
				<br style={{ display: 'none' }} dangerouslySetInnerHTML={{ __html: allStaticPixels }} />

				<Seo
					appData={appData}
					shareImage={shareImage}
					currentPage={currentPage}
				/>
			</body>
		</html>
	);
};

export default Doc;
