import { h } from 'preact';
// import { accessNested } from 'utils/utils';

export default function metaGeneratorTwitter(metaData, copy, buildType, useSiteHostedShareImage, canonicalBaseUrl) {
	if (!metaData || !Object.keys(metaData).length) {
		return [];
	}

	const twitterMetaData = { ...metaData };

	for (let key in twitterMetaData) {
		if (copy[twitterMetaData[key]]) {
			twitterMetaData[key] = copy[twitterMetaData[key]];
		}
	}

	if (useSiteHostedShareImage) {
		const cardImageFileName = twitterMetaData.cardImage.substring(twitterMetaData.cardImage.lastIndexOf('/')+1);
		twitterMetaData.cardImage = canonicalBaseUrl + '/' + cardImageFileName;
	}

	const tags = [
		twitterMetaData.cardSite && <meta name="twitter:site" content={`@${twitterMetaData.cardSite}`} />,
		twitterMetaData.via && <meta name="twitter:creator" content={`@${twitterMetaData.via}`} />,
		twitterMetaData.cardTitle && <meta name="twitter:title" content={twitterMetaData.cardTitle} />,
		twitterMetaData.cardImage && <meta name="twitter:image" content={twitterMetaData.cardImage} />,
		twitterMetaData.cardImageAlt && <meta name="twitter:image:alt" content={twitterMetaData.cardImageAlt} />,
	].filter(e => e);

	if (buildType === 'prod' && twitterMetaData.url) {
		tags.push(<meta name="twitter:url" content={twitterMetaData.url} />);
	}

	switch (twitterMetaData.cardType) {
		case 'summaryCard':
			tags.unshift(<meta name="twitter:card" content="summary" />);
			tags.push(<meta name="twitter:description" content={twitterMetaData.cardDescription} />);
			break;
		case 'summaryCardLargeImage':
			tags.unshift(<meta name="twitter:card" content="summary_large_image" />);
			tags.push(<meta name="twitter:description" content={twitterMetaData.cardDescription} />);
			break;
		case 'playerCard':
			tags.unshift(<meta name="twitter:card" content="player" />);
			tags.push(<meta name="twitter:description" content={twitterMetaData.cardDescription} />);
			tags.push(<meta name="twitter:player" content={twitterMetaData.playerVideo} />);
			tags.push(<meta name="twitter:player:width" content={twitterMetaData.playerWidth} />);
			tags.push(<meta name="twitter:player:height" content={twitterMetaData.playerHeight} />);
			break;
		default:
			tags.push(<meta name="twitter:description" content={twitterMetaData.description} />);
			break;
	}
	return tags;
}
