import { h } from 'preact';
// import { accessNested } from 'utils/utils';

export default function metaGeneratorFB(metaData, copy, buildType, useSiteHostedShareImage, canonicalBaseUrl) {
	if (!metaData) {
		return [];
	}

	const fbMetaData = { ...metaData };

	for (let key in fbMetaData) {
		if (copy[fbMetaData[key]]) {
			fbMetaData[key] = copy[fbMetaData[key]];
		}
	}

	if (fbMetaData.cardImage && !fbMetaData.cardSecureImage) {
		fbMetaData.cardSecureImage = fbMetaData.cardImage;
	}

	if (fbMetaData.cardSecureImage?.startsWith('//')) {
		fbMetaData.cardSecureImage = 'https:' + fbMetaData.cardSecureImage;
	}

	if (fbMetaData.cardImage && !fbMetaData.cardImageType) {
		fbMetaData.cardImageType = { png: 'image/png', jpg: 'image/jpeg' }[fbMetaData.cardImage.split('.').pop()];
	}

	if (useSiteHostedShareImage && fbMetaData.cardImage) {
		const cardImageFileName = fbMetaData.cardImage.split('/').pop();
		const cardSecureImageFileName = fbMetaData.cardSecureImage.split('/').pop();
		fbMetaData.cardImage = canonicalBaseUrl + '/' + cardImageFileName;
		fbMetaData.cardSecureImage = canonicalBaseUrl + '/' + cardSecureImageFileName;
	}

	const tags = [
		{ property: 'og:title', content: fbMetaData.title },
		{ property: 'og:description', content: fbMetaData.description },
		{ property: 'og:site_name', content: fbMetaData.siteName || fbMetaData.title },
		{ property: 'og:image', content: fbMetaData.cardImage },
		{ property: 'og:image:secure_url', content: fbMetaData.cardSecureImage },
		{ property: 'og:image:type', content: fbMetaData.cardImageType },
		{ property: 'og:image:width', content: fbMetaData.cardImageWidth },
		{ property: 'og:image:height', content: fbMetaData.cardImageHeight },
	].map(({ property, content }) => content && <meta property={property} content={content} />);

	if (buildType === 'prod') {
		tags.push(<meta property="og:url" content={`${fbMetaData.url || '' }`} />);
	}

	switch (fbMetaData.type) {
		case 'website':
			tags.push(<meta property="og:type" content="website" />);
	}
	return tags.filter(e => e);
}
