import { h, Component } from 'preact';
import { commaSeparatedList } from 'utils/utils';

// Use for any scripts that needed to be loaded in
// as the last tag before the </body>
export default class Scripts extends Component {

	getScriptsList() {
		return [
			// Should aw move onetrust out of <Scripts /> and into the <head> ? - probably should be loaded before any other script to block things properly
			this.oneTrust(),
			this.mailchimp(),
			this.constantContact(),
			this.userback(),
		].flat().filter(e => e);
	}

	oneTrust() {
		const appData = this.props.appData;
		const { waitForAgeGate, pageExclusions, scripts, crossOrigin } = appData?.options?.oneTrustCookieConsent || {};
		if (appData?.options?.ageGate && waitForAgeGate) {
			return;
		}
		if (commaSeparatedList(pageExclusions).includes(this.props.page?.id)) {
			return;
		}
		return Object.values(scripts || {}).map(el => {
			if (el.inlineContent) {
				return <script dangerouslySetInnerHTML={{ __html: el.inlineContent }} />;
			}

			return (
				<script
					charset="UTF-8"
					src={el.src}
					integrity={el.integrity}
					crossorigin={crossOrigin === 'none' ? undefined : crossOrigin || 'anonymous'}
					data-domain-script={el.dataDomainScript}
					async={el.async}
				/>
			);
		});
	}

	mailchimp() {
		// 2 modes (uuid or id) - a bit confusing, should we change that ?

		const data = this.props.appData?.options?.formEmbeds?.popup || {};
		if (data.uuid) {
			// New embed mode
			return (
				<script
					id="new-mcjs"
					src="//downloads.mailchimp.com/js/signup-forms/popup/unique-methods/embed.js"
					data-dojo-config="usePlainJson: true, isDebug: false"
					async
				/>
			);
		}

		const { id, ignoreClosedCookie, ignoreSubscribedCookie } = data;
		if (!id) {
			return;
		}
		return (
			<script
				id="mcjs"
				dangerouslySetInnerHTML={{
					__html: `
${ignoreClosedCookie ? 'document.cookie = "MCPopupClosed=; expires=Thu, 01 Jan 1970 00:00:01 UTC; path=/";' : ''}
${ignoreSubscribedCookie ? 'document.cookie = "MCPopupSubscribed=; expires=Thu, 01 Jan 1970 00:00:01 UTC; path=/";' : ''}
!function(c, h, i, m, p) {
m = c.createElement(h),
p = c.getElementsByTagName(h)[0],
m.async = 1, m.src = i,
p.parentNode.insertBefore(m, p)
}(document, "script", "https://chimpstatic.com/mcjs-connected/js/users/${id}.js");`
				}}
			/>
		);
	}

	constantContact() {
		const signUpData = this.props.appData?.options?.formEmbeds?.signUp || {};
		if (signUpData?.formType !== 'constantContact') {
			return;
		}
		const id = commaSeparatedList(signUpData.embedCode)[0];
		return [
			id && <script dangerouslySetInnerHTML={{ __html: `var _ctct_m=${JSON.stringify(id)};` }} />,
			<script defer id="signupScript" src="//static.ctctcdn.com/js/signup-form-widget/current/signup-form-widget.min.js" />
		];
	}

	userback() {
		if (!__IS_LOCAL__ && !__IS_DEMO__) {
			return;
		}
		const { accessToken, display } = this.props.appData?.options?.userbackFeedback || {};
		if (!accessToken || display !== true) {
			return;
		}
		return [
			<script dangerouslySetInnerHTML={{ __html: `window.Userback=window.Userback||{};Userback.access_token='${accessToken}'` }} />,
			<script async src="https://static.userback.io/widget/v1.js" />
		];
	}

	render({ appData }) {
		let list = this.getScriptsList(appData);
		// Should always be an array but just in case
		if (!list || (Array.isArray(list) && !list.length)) {
			return;
		}
		return <div>{list}</div>;
	}
}
